import React, {useContext} from "react";
import "../StartupProjects/StartupProjects.scss";
import {showcaseProjects} from "../../portfolio";
import {Fade} from "react-reveal";
import StyleContext from "../../contexts/StyleContext";
import Button from "../../components/button/Button";

export default function Showcase() {
  function openUrlInNewTab(url) {
    if (!url) {
      return;
    }
    var win = window.open(url, "_blank");
    win.focus();
  }

  const {isDark} = useContext(StyleContext);
  if (!showcaseProjects.display) {
    return null;
  }
  return (
    <Fade bottom duration={1000} distance="20px">
      <div className="main" id="showcase">
        <div>
          <h1 className="skills-heading">{showcaseProjects.title}</h1>
          <p
            className={
              isDark ? "dark-mode project-subtitle" : "subTitle project-subtitle"
            }
          >
            {showcaseProjects.subtitle}
          </p>

          <div className="projects-container">
            {showcaseProjects.projects.map((project, i) => {
              return (
                <div
                  key={i}
                  className={
                    isDark
                      ? "dark-mode project-card project-card-dark"
                      : "project-card project-card-light"
                  }
                >
                  {project.image ? (
                    <div className="project-image">
                      <img
                        src={project.image}
                        alt={project.projectName}
                        className="card-image"
                      ></img>
                    </div>
                  ) : null}
                  <div className="project-detail">
                    <h5
                      className={isDark ? "dark-mode card-title" : "card-title"}
                    >
                      {project.projectName}
                    </h5>
                    <p
                      className={
                        isDark ? "dark-mode card-subtitle" : "card-subtitle"
                      }
                    >
                      {project.projectDesc}
                    </p>
                    {/* Footer buttons removed per request */}
                  </div>
                </div>
              );
            })}
          </div>
          <Button
            text={"View showcase details and reflections on Drive"}
            className="project-button"
            href="https://drive.google.com/drive/folders/1UDfE0nfiY4fsvuFKWoTg_luHUwIcu22d?usp=sharing"
            newTab={true}
          />
        </div>
      </div>
    </Fade>
  );
}


